<template>
  <div class="video_con">
    <el-button type="primary" plain class="back" @click="backBtnClick">返回</el-button>
    <video :src="mvUrl" controls preload></video>
  </div>
</template>

<script>
import { getMV } from "../network/axios";
export default {
  data() {
    return {
      mvId: null,
      mvUrl: null,
    };
  },
  methods: {
    getData() {
      getMV(this.mvId).then((res) => {
        this.mvUrl = res.data.data.url;
      });
    },
    backBtnClick() {
      this.$router.go(-1);
    },
  },
  created() {
    this.mvId = this.$route.params.mvId;
    this.getData();
  },
};
</script>

<style lang="less">
.video_con {
  position: relative;
  height: 100%;
  background-image: linear-gradient(to right, #085f94, #43677d);

  video {
    box-shadow: 9px 10px 6px rgba(63, 63, 63, 1);

    margin-top: 30px;
    height: 500px;
  }
  .back {
    position: absolute;
    width: 100px;
    top: 23px;
    left: 25px;
  }
}
</style>